<template>
  <v-app>
    <div class="container" :class="st">
      <div class="navbar">
        <div class="menu">
          <h3 class="logo">Georges<span>Ayeni</span></h3>
          <div @click="toggle()" :class="`hamburger-menu`">
            <div class="bar"></div>
          </div>
        </div>
      </div>
      <v-main>
        <div class="main-container">
          <div class="main">
            <header>
              <div class="overlay">
                <div v-if="home">
                  <router-view></router-view>
                </div>
                <div v-else-if="web">
                  <WebPage />
                </div>
                <div v-else-if="pro">
                  <ProjectPage />
                </div>
                <div v-else-if="portfolio">
                  <PortePage />
                </div>
                <div v-else-if="contact">
                  <ContactPage />
                </div>
                <div v-else-if="about">
                  <AboutPage />
                </div>
                <div v-else>
                  <NotFound />
                </div>
              </div>
            </header>
          </div>
          <div class="shadow one"></div>
          <div class="shadow two"></div>
        </div>
        <div class="links">
          <ul>
            <li v-for="link in links" :key="link.value">
              <span class="test" @click="change('home')" style="--i: 0.05s"
                >{{ link.value }}</span
              >
            </li>
          </ul>
        </div>
      </v-main>
    </div>
  </v-app>
</template>

<script>
import WebPage from "@/views/WebsiteView.vue";
import ProjectPage from "@/views/ProVIew.vue";
import AboutPage from "@/views/AboutView.vue";
import PortePage from "@/views/PortfolioView.vue";
import ContactPage from "@/views/ContactView.vue";
import NotFound from "@/views/NotFound.vue";
export default {
  name: "App",
  components: {
    WebPage,
    ProjectPage,
    ContactPage,
    AboutPage,
    PortePage,
    NotFound,
  },
  created: {},
  destroyed: {},
  data() {
    return {
      links: [
        {
          value: "Home",
          lien: 'home',
        },
        {
          value: "Websites",
          lien: 'web',
        },
        {
          value: "Projects",
          lien: 'pro',
        },
        {
          value: "Portfolio",
          lien: 'port',
        },
        {
          value: "Contact-us !",
          lien: 'contact',
        },
        {
          value: "About us",
          lien: 'about',
        },
      ],
      home: true,
      web: false,
      pro: false,
      portfolio: false,
      contact: false,
      about: false,
      st: "",
      isActive: false,
    };
  },
  methods: {
    toggle() {
      this.isActive = !this.isActive;
      if (this.isActive) {
        this.st = "active";
      } else {
        this.st = "";
      }
    },
    change(val) {
      if (this.isActive) {
        this.st = "beforex";
        setTimeout(() => {
          switch (val) {
            case "home":
              this.home = true;
              this.web = false;
              this.pro = false;
              this.portfolio = false;
              this.contact = false;
              this.about = false;
              break;

            case "web":
              this.home = false;
              this.web = true;
              this.pro = false;
              this.portfolio = false;
              this.contact = false;
              this.about = false;
              break;

            case "projet":
              this.home = false;
              this.web = false;
              this.pro = true;
              this.portfolio = false;
              this.contact = false;
              this.about = false;
              break;

            case "port":
              this.home = false;
              this.web = false;
              this.pro = false;
              this.portfolio = true;
              this.contact = false;
              this.about = false;
              break;

            case "contact":
              this.home = true;
              this.web = false;
              this.pro = false;
              this.portfolio = false;
              this.contact = true;
              this.about = false;
              break;

            case "about":
              this.home = false;
              this.web = false;
              this.pro = false;
              this.portfolio = false;
              this.contact = false;
              this.about = true;
              break;

            default:
              this.home = false;
              this.web = false;
              this.pro = false;
              this.portfolio = false;
              this.contact = false;
              this.about = false;
              break;
          }
          this.toggle();
        }, 1600);
      }
    },
  },
};
</script>
